body{
  background-color: blue;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

h1 {
  color: white;
  text-transform: uppercase;
  text-align: center;
  margin-top: 2rem;
  font-weight: bold;
}
.container{
  background-color: white;
  padding: 1rem;
  border-radius: 1rem;
  margin-top: 2rem;
  min-height: 70vh;
  text-align: center;
  width: 50%;
  margin-left: 20rem;
}
form{
  margin: 0 auto;
  max-width: 600px;
  display: flex;
  flex-direction: column;

}
form label{
  text-align: center;
  font-size: 1.5rem;
}
.full{
  width: 60%;
  height: 30px;
}
.button{
  background-color: brown;
  width: 100px;
  border-radius: 25px;
  height: 30px;
  margin-top: 20px;
  color: white;
}
.button:hover{
  background-color: rgba(233, 3, 3, 0.932);
}
input[type="submit"]:disabled{
  opacity: .5;
}